import Vue from 'vue'
import VueRouter from 'vue-router'
import Content from '@/views/Content'
import Index from '@/views/Index'
import Assess from '@/views/Assess'
import Price from '@/views/Price'
import Login from '@/views/Login'
import Register from '@/views/Register'

import PersonalCenter from '@/views/Personal-center'
import address from '@/components/address'
import newaddress from '@/components/newaddress'
import Account from '@/components/Account'
import volume from '@/components/volume'
import Balance from '@/components/Balance'
import Terms from '@/components/Terms'
import Order from '@/components/Order'
import Return from '@/components/Return'
import edit from '@/components/edit'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'index',
    components: {
      index: Index
    }
  },
  {
    path: '/content',
    name: 'content',
    components: {
      default: Content
    }
  },
  {
    path: '/price',
    name: 'price',
    components: {
      default: Price
    }
  },
  {
    path: '/assess',
    name: 'assess',
    components: {
      default: Assess
    }
  },


  {
    path: '/PersonalCenter',
    name: 'PersonalCenter',
    components: {
      default: PersonalCenter
    },
    alias: '/Personal-center',
    beforeEnter: (to, from, next) => {
      let login = window.localStorage.getItem('login')
      if (login) {
        next()
      } else {
        next({
          path: '/login'
        })
      }
    }
  },
  {
    path: '/address',
    name: 'address',
    components: {
      default: address
    },
  },
  {
    path: '/newaddress',
    name: 'newaddress',
    components: {
      default: newaddress
    },
  },
  {
    path: '/Account',
    name: 'Account',
    components: {
      default: Account
    },
  },
  {
    path: '/volume',
    name: 'volume',
    components: {
      default: volume
    },
  },
  {
    path: '/Balance',
    name: 'Balance',
    components: {
      default: Balance
    },
  },
  {
    path: '/Terms',
    name: 'Terms',
    components: {
      default: Terms
    },
  },
  {
    path: '/Order',
    name: 'Order',
    components: {
      default: Order
    },
  },
  {
    path: '/Return',
    name: 'Return',
    components: {
      default: Return
    },
  },
  {
    path: '/edit',
    name: 'edit',
    components: {
      default: edit
    },
  },
  {
    path: '/login',
    name: 'login',
    components: {
      default: Login
    },
  },
  {
    path: '/register',
    name: 'register',
    components: {
      default: Register
    },
  },


]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router