<template>
  <div style="font-size: 12px;">
    <div class="g-basic-body">
      <div class="g-basic-title">
        <router-link to="/address" class="go-back"></router-link>
        <div class="title">编辑地址</div>
        <div class="save-btn" @click="leSave">保存</div>
      </div>
      <div class="address-info">
        <ul>
          <li>
            <div>收货人：</div>
            <div>
              <input
                v-model="addressData[idx].username"
                id="consignee"
                type="text"
                placeholder="请输入收货人姓名"
              />
            </div>
          </li>
          <li>
            <div>联系电话：</div>
            <div>
              <input
                v-model="addressData[idx].phone"
                id="mobile"
                type="text"
                placeholder="请输入联系电话"
              />
            </div>
          </li>

          <li>
            <div>所在省：</div>
            <div>
              <select v-model="lv1" id="province-list" name="province" @change="handleSelect1">
                <option :value="-1">请选择省</option>
                <option v-for="(item,index) in loc1" :key="index" :value="index">{{item.Name}}</option>
              </select>
            </div>
          </li>
          <li>
            <div>所在市：</div>
            <div>
              <select v-model="lv2" id="city-list" name="city" @change="handleSelect2">
                <option :value="-1">请选择市</option>
                <option v-for="(item,index) in loc2" :key="index" :value="index">{{item.Name}}</option>
              </select>
            </div>
          </li>
          <li>
            <div>所在区：</div>
            <div>
              <select v-model="lv3" id="distract-list" name="distract">
                <option :value="-1">请选择区</option>
                <option v-for="(item,index) in loc3" :key="index" :value="index">{{item.Name}}</option>
              </select>
            </div>
          </li>
          <li>
            <div>详细地址：</div>
            <div>
              <input v-model="addressData[idx].address" id="address" type="text" placeholder="请输入详细地址" />
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import cityData from "@/assets/js/city.js";
  export default {
    data() {
      return {
        loc1: cityData,
        lv1: -1,
        lv2: -1,
        lv3: -1,       
      };
    },
    created() {
      this.lv1 = this.addressData[this.idx].loc[0].num;
      this.lv2 = this.addressData[this.idx].loc[1].num;
      this.lv3 = this.addressData[this.idx].loc[2].num;      
    },
    computed: {
      loc2() {
        return this.lv1 === -1 ? [] : this.loc1[this.lv1].City;
      },
      loc3() {
        return this.lv2 === -1 ? [] : this.loc2[this.lv2].Region;
      },
      city() {
        return this.lv1 === -1 ? [] : this.loc1[this.lv1].Name;
      },
      city1() {
        return this.lv2 === -1 ? [] : this.loc2[this.lv2].Name;
      },
      city3() {
        return this.lv1 === -1 ? [] : this.loc3[this.lv3].Name;
      },
      address() {
        return this.$store.state.address;
      },
      addressData() {        
        return this.$store.state.addressData;
      },
      idx() {
        return this.$store.state.q1;
      }
    },
    methods: {
      handleSelect1() {
        this.lv2 = this.lv3 = -1;
      },
      handleSelect2() {
        this.lv3 = -1;
      },
      leSave() {     
        let newLoc = this.addressData.loc;
        newLoc= [
          {
            text: this.city,
            num: this.lv1
          },
          {
            text: this.city1,
            num: this.lv2
          },
          {
            text: this.city2,
            num: this.lv3
          }
        ];
        this.$store.commit("Mut", newLoc);   
        this.$router.push("/address");        
      }
    }
  };
</script>

<style>
</style>