<template>
  <div class="right-nav">
    <div class="scroller" style="transform: translate(0px, 0px); transition-duration: 200ms;">
      <ul>
        <li v-for="(item,index) in Rightlist" :key="index">
          <div v-show="Rightlist.length!==1">
            <span>{{index+1}}</span>
          </div>
          <div @click="assess('/assess',item,Rightid[index])">{{item}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    Rightlist() {
      return this.$store.state.Rightlist;
    },
    Rightid() {
      return this.$store.state.Rightid;
    }
  },
  methods: {
    assess(pathstr, item, id) {
      this.$store.commit("assMut", { idx: id, name: item });
      this.$router.push({ path: pathstr });
    }
  }
};
</script>

<style>
.right-nav {
  position: absolute;
  /* height: 100%; */
  background: #fff;
}
.right-nav {
  left: 24%;
  right: 0;
  box-sizing: border-box;
  padding-left: 4%;
}
.right-nav ul li > div {
  height: 100%;
  box-sizing: border-box;
}
.right-nav ul li > div:first-child {
  max-width: 6%;
  overflow: hidden;
}
.right-nav ul li > div:last-child {
  width: 85%;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.right-nav ul li {
  font-size: 0.12rem;
  height: 0.3rem;
  line-height: 0.3rem;
  border-bottom: 1px solid #d8d8d8;
}
.right-nav ul li div {
  display: inline-block;
  vertical-align: middle;
}
.right-nav ul li div:first-child {
  min-width: 0.2rem;
  height: 0.2rem;
  margin: 0rem 2%;
  border-radius: 4px;
  background: #e3e3e3;
  color: #fff;
  line-height: 0.2rem;
  border-radius: 2px;
  text-align: center;
  font-size: 0.12rem;
  padding: 0 0.02rem;
  box-sizing: border-box;
}
.right-nav ul li div:first-child span {
  display: block;
  -webkit-text-size-adjust: none;
  font-size: 0.1rem;
  line-height: 0.2rem;
}
.right-nav ul li:nth-child(1) div:first-child {
  background: #ff6e6e;
}
.right-nav ul li:nth-child(2) div:first-child {
  background: #ff9402;
}
.right-nav ul li:nth-child(3) div:first-child {
  background: #68bdf0;
}
</style>