import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isOn: 0, //控制SideBar组件中li的class
    Nav: 0,
    Leftlist: [],
    Leftid: [],
    Rightlist: [],
    Rightid: [],
    mod: '', //控制估价的产品型号    
    list: [],
    list2: [],
    list3: [],
    info: [],
    price: 0,
    q1: 0,
    addressData: [],
    tel: '',
  },
  mutations: {
    phoneMut(state, leftid) { //Index组件触发的Mutation
      setTimeout(() => {
        state.isOn = leftid;
        axios({ //获取第一个手机的型号数据
          url: `https://m.jjhuigou.com/ub-webapp/product/findCateByBrand?page=1&pageSize=30&brandId=${leftid}`,
          method: 'get',
        }).then(result => {
          if (result.data.message) { //判断是否有型号数据
            state.Rightlist = ["未搜索到型号，查看下其他品牌吧"];
          } else {
            let items = result.data.data.items;
            state.Rightlist = [];
            state.Rightid = [];
            items.forEach(obj => {
              state.Rightlist.push(obj.name);
              state.Rightid.push(obj.id);
            });
          }
        });
      }, 100);
    },
    showMut(state, i) { //Nav组件点击触发的mutation      
      if (i === 0) { //获取手机数据        
        state.isOn = 2;
        axios({
          url: 'https://m.jjhuigou.com/ub-webapp/product/getBrandByPage?cateType=0&page=1&pageSize=200',
          method: 'get',
        }).then(result => {
          let items = result.data.data.items;
          state.Leftlist = [];
          state.Leftid = [];
          items.forEach(obj => {
            state.Leftlist.push(obj.name);
            state.Leftid.push(obj.id);
          });
        });

        axios({ //获取第一个手机的型号数据
          url: `https://m.jjhuigou.com/ub-webapp/product/findCateByBrand?page=1&pageSize=30&brandId=2`,
          method: 'get',
        }).then(result => {
          if (result.data.message) { //判断是否有型号数据
            state.Rightlist = ["未搜索到型号，查看下其他品牌吧"];
          } else {
            let items = result.data.data.items;
            state.Rightlist = [];
            state.Rightid = [];
            items.forEach(obj => {
              state.Rightlist.push(obj.name);
              state.Rightid.push(obj.id);
            });
          }
        });
      }
      if (i === 1) { //获取平板数据       
        state.isOn = 25089;
        axios({
          url: 'https://m.jjhuigou.com/ub-webapp/product/getBrandByPage?cateType=1&page=1&pageSize=200',
          method: 'get',
        }).then(result => {
          let items = result.data.data.items;
          state.Leftlist = [];
          state.Leftid = [];
          items.forEach(obj => {
            state.Leftlist.push(obj.name);
            state.Leftid.push(obj.id);
          });
        });

        axios({ //获取第一个平板的型号数据
          url: `https://m.jjhuigou.com/ub-webapp/product/findCateByBrand?page=1&pageSize=30&brandId=25089`,
          method: 'get',
        }).then(result => {
          if (result.data.message) { //判断是否有型号数据
            state.Rightlist = ["未搜索到型号，查看下其他品牌吧"];
          } else {
            let items = result.data.data.items;
            state.Rightlist = [];
            state.Rightid = [];
            items.forEach(obj => {
              state.Rightlist.push(obj.name);
              state.Rightid.push(obj.id);
            });
          }
        });
      }
    },
    modMut(state, id) { //SideBar组件点击触发的mutation
      state.isOn = id;
      axios({
        url: `https://m.jjhuigou.com/ub-webapp/product/findCateByBrand?page=1&pageSize=30&brandId=${id}`,
        method: 'get',
      }).then(result => {
        if (result.data.message) { //判断是否有型号数据
          state.Rightlist = ["未搜索到型号，查看下其他品牌吧"];
        } else {
          let items = result.data.data.items;
          state.Rightlist = [];
          state.Rightid = [];
          items.forEach(obj => {
            state.Rightlist.push(obj.name);
            state.Rightid.push(obj.id);
          });
        }
      });
    },
    NavMut(state, num) { //Index组件点击触发的mutation
      state.Nav = num;
    },
    assMut(state, obj) { //Mod组件点击触发的mutation
      state.mod = obj.name,
        axios({
          url: `https://m.jjhuigou.com/ub-webapp/product/getSkuCate?cateGoryId=${obj.idx}`,
          method: 'get',
        }).then(result => {
          state.list = result.data.data[0].list;
          state.list2 = result.data.data[1].list;
          state.list3 = result.data.data[2].list;
        });
    },
    PriMut(state, arr) {
      state.info = arr;
      let radompri = parseInt(Math.random() * (3000 - 200 + 1) + 200);
      state.price = radompri;
    },

    Mut(state, obj) {
      state.addressData[state.q1].loc = obj;
    },
    addlocMut(state, obj) {
      state.addressData.push(obj)
    },
    delMut(state, idx) {
      state.addressData.splice(idx, 1)
    },
    adelMut(state, idx) {
      state.q1 = idx;
    },
    telMut(state, i) {
      state.tel = i;
    },
  },

})