<template>
  <div>
    <div class="g-basic-title">
      <div class="go-back" @click="back('/')"></div>
      <div class="m-cate-type">
        <ul>
          <li :class="isActive==0?'on':''" @click="handleshow(0)">手机回购</li>
          <li :class="isActive==1?'on':''" @click="handleshow(1)">平板回购</li>
        </ul>
      </div>
      <div class="search-btn"></div>
    </div>
    <div class="m-cate-title">
      <div>品牌</div>
      <div>型号</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isActive: 0
    };
  },
  methods: {
    handleshow(i) {
      if (i == 0) {
        this.isActive = 0;
      } else {
        this.isActive = 1;
      }
      this.$store.commit("showMut", i);
    },
    back(pathstr) {
      this.$router.push({ path: pathstr });
    }
  },
  computed: {
    Nav() {
      return this.$store.state.Nav;
    }
  },
  created() {
    //初始化数据，让页面进来就加载手机数据渲染
    this.handleshow(this.Nav);
  }
};
</script>

<style>
html {
  font-size: 26.67vw;
}

.g-basic-title {
  height: 0.45rem;
  line-height: 0.45rem;
  background: #fff;
  border-bottom: 1px solid #d8d8d8;
  box-sizing: border-box;
  position: relative;
  z-index: 10;
}
.g-basic-title .go-back {
  height: 100%;
  width: 0.4rem;
  position: absolute;
  left: 0;
  top: 0;
  background: url("../assets/img/back.png") no-repeat center center;
  background-size: 0.14rem 0.2rem;
}

.m-cate-type {
  width: 50%;
  height: 0.3rem;
  line-height: 0.3rem;
  margin: 0 auto;
  font-size: 0;
  transform: translateY(0.05rem);
  border: 1px solid #49c979;
  border-radius: 4px;
  color: #595959;
}
.m-cate-type ul {
  height: 0.3rem;
}
.m-cate-type ul li {
  height: 0.3rem;
  font-size: 0.12rem;
  display: inline-block;
  vertical-align: middle;
  width: 50%;
  text-align: center;
  box-sizing: border-box;
}
.m-cate-type ul li.on {
  background: #49c979;
  color: #fff;
}

.search-btn {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 0.4rem;
  background: url("../assets/img/search.png") no-repeat center center;
  background-size: 0.24rem 0.24rem;
}

.m-cate-title {
  height: 0.45rem;
  line-height: 0.45rem;
  width: 100%;
  z-index: 10;
  font-size: 0;
  box-sizing: border-box;
  border-bottom: 1px solid #d8d8d8;
}

.m-cate-title > div {
  display: inline-block;
  vertical-align: middle;
  font-size: 0.14rem;
  background: #fff;
  height: 100%;
}
.m-cate-title > div:first-child {
  width: 24%;
  text-align: center;
}
.m-cate-title > div:last-child {
  width: 76%;
  padding-left: 4%;
  box-sizing: border-box;
}
</style>