<template>
  <div>
    <div class="g-basic-title">
      <div class="go-back" @click="back('/content')"></div>
      <div class="title">
        评估
        <span>{{mod}}</span>
      </div>
    </div>
    <div class="g-basic-content">
      <div>
        <div
          class="m-cconfig-list show"
          v-for="(item,index) in list"
          :key="item.id"
          :class="checkid[index]===list[index].id?'checked':''"
        >
          <div class="title">
            {{item.name}}
            <span>
              :
              <span ref="span"></span>
            </span>
          </div>
          <div class="update-icon" @click="gai(1,index)">修改</div>
          <ul class="list-info" ref="ul">
            <li v-for="(item2) in item.paramList" :key="item2.id">
              <span
                @click="write(item2.name,list[index].id,index,item2.id)"
                :class="showid==item2.id?'show':''"
              >{{item2.name}}</span>
            </li>
          </ul>
        </div>
        <div
          class="m-cconfig-list show"
          v-for="(item,index) in list2"
          :key="item.id"
          :class="checkid1[index]===list2[index].id?'checked':''"
        >
          <div class="title">
            {{item.name}}
            <span>
              :
              <span ref="span1"></span>
            </span>
          </div>
          <div class="update-icon" @click="gai(2,index)">修改</div>
          <ul class="list-info" ref="ul1">
            <li v-for="(item2) in item.paramList" :key="item2.id">
              <span
                @click="write1(item2.name,list2[index].id,index,item2.id)"
                :class="showid1==item2.id?'show':''"
              >{{item2.name}}</span>
            </li>
          </ul>
        </div>
        <div class="m-cconfig-list show">
          <div class="title">功能情况（可多选或不选）</div>
          <div class="update-icon">修改</div>
          <ul class="list-info">
            <li v-for="(item,index) in list3" :key="item.id">
              <span :id="item.id" ref="span3" @click="sel(item.id,index)">{{item.name}}</span>
            </li>
          </ul>
        </div>
      </div>
      <div class="select-price" @click="price('/price',mod)">查看价格</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkid: [],
      checkid1: [],
      showid: 0,
      showid1: 0,
      i: 0, //记录点击修改的次数
      j: 0, //记录点击修改的次数
      info: []
    };
  },
  computed: {
    mod() {
      return this.$store.state.mod;
    },
    list() {
      return this.$store.state.list;
    },
    list2() {
      return this.$store.state.list2;
    },
    list3() {
      return this.$store.state.list3;
    }
  },
  methods: {
    back(pathstr) {
      this.$router.push({ path: pathstr });
    },
    price(pathstr) {
      let span = this.$refs.span;
      let span1 = this.$refs.span1;
      let span3 = this.$refs.span3;
      span.forEach(obj => {
        this.info.push(obj.innerHTML);
      });
      span1.forEach(obj => {
        this.info.push(obj.innerHTML);
      });
      span3.forEach(obj => {
        if (obj.className == "show") {
          this.info.push(obj.innerHTML);
        }
      });
      // console.log(this.info);
      this.$store.commit("PriMut", this.info);
      this.$router.push({ path: pathstr });
    },
    write(val, id, index, id1) {
      this.showid=id1;
      this.checkid.push(id);
      if (this.list[index].id === id) {
        this.$refs.span[index].innerHTML = val;
      }
    },
    write1(val, id, index, id2) {
      this.showid1=id2;
      this.checkid1.push(id);
      if (this.list2[index].id === id) {
        this.$refs.span1[index].innerHTML = val;
      }
    },
    sel(id, i) {
      let idx = this.$refs.span3[i].getAttribute("id");
      if (id == idx) {
        this.$refs.span3[i].className = "show";
      }
    },
    gai(n, i) {
      if (n == 1) {
        this.i++;
        if (this.i % 2 == 1) {
          this.$refs.ul[i].style.display = "block";
        } else {
          this.$refs.ul[i].style.display = "none";
        }
      } else {
        this.j++;
        if (this.j % 2 == 1) {
          this.$refs.ul1[i].style.display = "block";
        } else {
          this.$refs.ul1[i].style.display = "none";
        }
      }
    }
  }
};
</script>
<style scoped>
html {
  font-size: 26.67vw;
}
.g-basic-title {
  height: 0.45rem;
  line-height: 0.45rem;
  background: #fff;
  border-bottom: 1px solid #d8d8d8;
  box-sizing: border-box;
  position: relative;
  z-index: 10;
}
.go-back {
  height: 100%;
  width: 0.4rem;
  position: absolute;
  left: 0;
  top: 0;
  background: url("../assets/img/back.png") no-repeat center center;
  background-size: 0.14rem 0.2rem;
}
.g-basic-title .title {
  font-size: 0.14rem;
  color: #595959;
  text-align: center;
}
.g-basic-content {
  position: absolute;
  top: 0.45rem;
  bottom: 0;
  width: 100%;
  overflow: auto;
}
.m-cconfig-list {
  position: relative;
  background: #fff;
  padding: 0.2rem;
  border-bottom: 1px solid #d8d8d8;
  font-size: 0;
}
.m-cconfig-list .update-icon {
  display: none;
  position: absolute;
  right: 0.4rem;
  top: 0;
  color: #49c979;
  width: 0.4rem;
  height: 0.2rem;
  font-size: 0.12rem;
  text-align: center;
  padding: 0.2rem 0;
}
.m-cconfig-list.checked .update-icon {
  display: block;
}

.m-cconfig-list .title {
  color: #333;
  font-size: 0.13rem;
  pointer-events: none;
}
.m-cconfig-list .title span span {
  color: #49c979;
}
.m-cconfig-list.show .list-info {
  display: block;
}
.m-cconfig-list.checked .list-info {
  display: none;
}
.list-info {
  display: none;
  margin-top: 0.2rem;
}
.list-info li {
  display: inline-block;
}
.list-info li span {
  display: inline-block;
  vertical-align: middle;
  font-size: 0.12rem;
  color: #595959;
  border: 1px solid #979797;
  border-radius: 0.14rem;
  text-align: center;
  height: 0.2rem;
  line-height: 0.2rem;
  padding: 0.06rem 0.16rem;
  margin: 0.03rem 0.06rem;
}
.list-info li span.show {
  background: #28ca65;
  color: #fff;
  border: 1px solid #28ca65;
}
.select-price {
  display: block;
  height: 0.45rem;
  line-height: 0.45rem;
  font-size: 0.14rem;
  background: #ff9402;
  color: #fff;
  text-align: center;
  border-radius: 4px;
  margin: 0.12rem auto;
  width: 90%;
}
</style>