      list2: []
    };
  },
  methods: {
    man(pathstr) {
      this.$router.push({ path: pathstr });
    },
    jump(pathstr, nav) {
      this.$router.push({ path: pathstr });
      this.$store.commit("NavMut", nav);
    },
    jump1(pathstr, leftid) {
      this.$router.push({ path: pathstr });
      this.$store.commit("phoneMut", leftid);
    }
  },
  components: {
    swiper
  },
  created() {
    this.$axios({
      url:
        "https://m.jjhuigou.com/ub-webapp/product/getBrandByPage?cateType=0&page=1&pageSize=16",
      method: "get"
    }).then(res => {
      this.list = res.data.data.items;
      // console.log(this.list[1].name);
    });
    this.$axios({
      url:
        "https://m.jjhuigou.com/ub-webapp/home/orders_flowing?pageNo=1&pageSize=10",
      method: "get"
    }).then(res => {
