<template>
  <div class="left-nav">
    <div class="scroller" style="transform: translate(0px, 0px); transition-duration: 200ms;">
      <ul>
        <li
          v-for="(item,index) in Leftlist"
          :key="index"
          :class="isOn===Leftid[index]?'on':''"
          @click="change(Leftid[index])"
        >{{item}}</li>
      </ul>
    </div>
  </div>
</template>
// ||isOn[0].righton===Leftid[index]
<script>
export default { 
  computed: {
    Leftlist() {
      return this.$store.state.Leftlist;
    },
    Leftid() {
      return this.$store.state.Leftid;
    },
    isOn() {
      return this.$store.state.isOn;
    }
  },
  methods: {
    change(id) {
      this.$store.commit("modMut", id);
    }
  }
};
</script>

<style>
.left-nav {
  position: absolute;
  /* height: 100%; */
  background: #fff;
}
.left-nav {
  left: 0;
  right: 76%;
  background: #f6f6f6;
}

.left-nav ul li {
  font-size: 0.12rem;
  height: 0.3rem;
  line-height: 0.3rem;
  border-bottom: 1px solid #d8d8d8;
}

.left-nav ul li.on {
  background: #fff;
  color: #595959;
}
.left-nav ul li {
  text-align: center;
  color: #595959;
}
</style>